<script>
export default {
  name: "CalcSpeed",
  head: {
    title() {
      return {
        inner: this.title
      };
    },
    meta() {
      return [
        { name: "title", content: this.title },
        { name: "description", content: this.description },
        { property: "og:title", content: this.title },
        { property: "og:description", content: this.description }
      ];
    }
  },
  data() {
    return {
      title: "素早さ計算機（ポケモン剣盾に対応）",
      description:
        "ポケモン剣盾に対応している素早さ計算機です。実数値を入力することで、あらゆる状態の素早さをリアルタイムに表示します。",
      speed: 100,
      tailwind: 1,
      scarf: 1,
      paralysis: 1,
      macho: 1,
      swamp: 1,
      option1: false,
      option2: false
    };
  },
  computed: {
    calcSpeed() {
      return Math.floor(
        this.speed *
          this.tailwind *
          this.scarf *
          this.paralysis *
          this.macho *
          this.swamp
      );
    }
  }
};
</script>
